import React from 'react';
import './App.css';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import axios from 'axios';

import 'bootstrap/dist/css/bootstrap.min.css';

class App extends React.Component {

  constructor() {
    super();
    
    this.state = ({
      readyState: 0
    });

    this.fetchJsonFiles();
  }

  fetchJsonFiles() {
    axios.get('https://raw.githubusercontent.com/wa-vaccine/wa/master/vaccine_locations.json')
    .then((res) => {
      var locationDictionary = {};

      var locationsData = res.data;

      var locsShorthand = locationsData["locations"];
      for(var i = 0; i < locsShorthand.length; i++) {
        locationDictionary[locsShorthand[i]["name"] + " " + locsShorthand[i]["url"]] = locsShorthand[i];
      }
      if(this.state.readyState < 2) {
        this.setState({
          locationDictionary: locationDictionary,
          readyState: this.state.readyState + 1
        });
      }
    });
    axios.get("https://raw.githubusercontent.com/wa-vaccine/wa/master/vaccine_statuses.json")
    .then((res) => {
      if(this.state.readyState < 2) {
        this.setState({
          statusData: res.data,
          readyState: this.state.readyState + 1
        });
      }
    });
  }

  updateDataAndState() {
    if(this.state.readyState !== 2) {
      return;
    }

    var now = new Date();

    axios.get("https://raw.githubusercontent.com/wa-vaccine/wa/master/last_update_stamp.json")
      .then((res) => {
        // console.log("COMPARE ");
        // console.log("1: " + res.data["lastScrapeUpdate"]);
        // console.log("2: " + this.state.lastWebsiteUpdateTime);
        if(res.data["lastScrapeUpdate"] !== this.state.lastWebsiteUpdateTime) {
          // console.log("UPDATE");
          this.setState({
            lastWebsiteUpdateTime: res.data["lastScrapeUpdate"]
          });
          this.fetchJsonFiles();
        }
      });

    var shouldUpdateFile = true;
    if(this.state.lastRescrapeRenderTime != null) {
      shouldUpdateFile = Date.parse(this.state.lastWebsiteUpdateTime) - this.state.lastRescrapeRenderTime !== 0; 
    }

    if(shouldUpdateFile) {
      var statusVal = [];

      this.available = 0;
      var statuses = this.state.statusData["statuses"];
      for(var i = 0; i < statuses.length; i++) {
        var data = statuses[i]["serialized"];

        if(data["availability"] === "AVAILABLE") {
          this.available++;
          var locEntry = this.state.locationDictionary[statuses[i]["loc"]];
          var locDetails = locEntry["county"] + "\n" + (locEntry["address"] === "" ? locEntry["city"] : locEntry["address"]) + "\n" + locEntry["phoneNumber"] + "\n" + locEntry["email"];

          statusVal.push(this.getCard(i + 1, locEntry["name"], locDetails, data["currentUrl"]));
        }
      }

      this.setState({
          lastRescrapeRenderTime: Date.parse(this.state.lastWebsiteUpdateTime),
          status: statusVal
      });
    }

    var secondsAgo = Math.floor((now - this.lastScrapeUpdate) / 1000);
    var minutesAgo = Math.floor(secondsAgo / 60);
    var hoursAgo = Math.floor(minutesAgo / 60);

    var lastUpdateTimeTextVal;
    if(secondsAgo < 60) {
      lastUpdateTimeTextVal = "Last updated " + secondsAgo + " second" + (secondsAgo === 1 ? "" : "s") + " ago"
    } else if(minutesAgo < 60) {
      lastUpdateTimeTextVal = "Last updated " + minutesAgo + " minute" + (minutesAgo === 1 ? "" : "s") + " ago"
    } else {
      lastUpdateTimeTextVal = "Last updated " + hoursAgo + " hour" + (hoursAgo === 1 ? "" : "s") + " ago"  
    }

    var apptTextVal;
    if(this.available === 0) {
      apptTextVal = "There are currently no locations that are accepting appointments."
    } else if (this.available === 1) {
      apptTextVal = "There is currently 1 location that is accepting appointments:";
    } else {
      apptTextVal = "There are currently " + this.available + " locations that are accepting appointments:";
    }

    this.setState({
      lastUpdateTimeText: lastUpdateTimeTextVal,
      apptText: apptTextVal
    });
  }

  componentDidMount() {
    this.updateDataAndState();

    this.timerID = setInterval(
      () => {
        this.updateDataAndState();
      }, 1000
    );
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <br></br>
          <h1 style={{fontSize: "1.8em"}}>
            Where can I get the vaccine?
          </h1>
          <h3 style={{fontSize: "0.7em"}}>
            WASHINGTON STATE
            <br></br>
            Currently available for ages 65 years and older.
          </h3>
          <br></br>
          <p style={{fontSize: "1em"}}>
            {this.state.readyState !== 2 ? "Loading..." : this.state.apptText}
          </p>
        </header>
        <body>
          {this.state.status}
        </body>
        <p style={{fontSize: "1em", color: "#cccccc"}}>
            {this.state.lastUpdateTimeText}
          </p>
      </div>
    );
  }

  getCard(index, name, details, url) {
    return (
      <Card style={{ width: '95%', marginLeft: "2.5%", marginRight: "2.5%", marginBottom: "0.8rem" }}>
            <Card.Body>
              <Card.Title style={{fontWeight: "bold"}}>{index}. {name}</Card.Title>
              <Card.Text style={{marginLeft: "1.5rem", lineHeight: "1rem"}}>
                {details.split("\n").map((item, i) => <p key={i}>{item}</p>)}
              </Card.Text>
              <Button style={{ width: '100%' }} variant="primary" target="_blank" href={url}>Visit site</Button>
            </Card.Body>
          </Card>
    );
  }
}

export default App;
